/**
 * Log rotation utilities
 *
 * Provides date-based log file paths and automatic cleanup of stale logs.
 */

import { readdir, unlink } from "node:fs/promises";
import { join } from "node:path";
import { LOG_RETENTION_DAYS, LOGS_DIR } from "../config/constants.js";
import { ensureDirectory } from "./fs-utils.js";
import { logger } from "./logger.js";

// Throttle cleanup to run at most once per hour
const CLEANUP_THROTTLE_MS = 60 * 60 * 1000; // 1 hour
let lastCleanupTime: Record<string, number> = {};

/**
 * Get current date string in YYYY-MM-DD format (UTC)
 */
export function getDateString(): string {
  return new Date().toISOString().split("T")[0];
}

/**
 * Get date-based log file path
 * @param logPrefix - Prefix for the log file (e.g., "plugin" or "sync")
 * @returns Full path like ~/.claude-zest/logs/plugin-2026-01-09.log
 */
export function getDatedLogPath(logPrefix: string): string {
  const dateStr = getDateString();
  return join(LOGS_DIR, `${logPrefix}-${dateStr}.log`);
}

/**
 * Parse date from log filename
 * @param filename - Filename like "plugin-2026-01-09.log"
 * @param logPrefix - Prefix to match (e.g., "plugin")
 * @returns Date object or null if doesn't match pattern
 */
function parseDateFromFilename(filename: string, logPrefix: string): Date | null {
  const pattern = new RegExp(`^${logPrefix}-(\\d{4}-\\d{2}-\\d{2})\\.log$`);
  const match = filename.match(pattern);

  if (!match) {
    return null;
  }

  const date = new Date(match[1] + "T00:00:00Z");
  return Number.isNaN(date.getTime()) ? null : date;
}

/**
 * Clean up stale log files older than retention period
 * Throttled to run at most once per hour per logPrefix
 *
 * @param logPrefix - Prefix of logs to clean (e.g., "plugin" or "sync")
 */
export async function cleanupStaleLogs(logPrefix: string): Promise<void> {
  const now = Date.now();
  const lastCleanup = lastCleanupTime[logPrefix] || 0;

  // Throttle: skip if cleaned up less than an hour ago
  if (now - lastCleanup < CLEANUP_THROTTLE_MS) {
    return;
  }

  lastCleanupTime[logPrefix] = now;

  try {
    await ensureDirectory(LOGS_DIR);
    const files = await readdir(LOGS_DIR);
    const cutoffDate = new Date(now - LOG_RETENTION_DAYS * 24 * 60 * 60 * 1000);

    for (const file of files) {
      const fileDate = parseDateFromFilename(file, logPrefix);

      if (fileDate && fileDate < cutoffDate) {
        const filePath = join(LOGS_DIR, file);
        try {
          await unlink(filePath);
        } catch (error) {
          logger.error(`Failed to delete old log file ${file}`, error);
        }
      }
    }
  } catch (error) {
    logger.error("Failed to cleanup old logs", error);
  }
}

/**
 * Force cleanup regardless of throttle (for daemon startup)
 * @param logPrefix - Prefix of logs to clean (e.g., "plugin" or "sync")
 */
export async function forceCleanupStaleLogs(logPrefix: string): Promise<void> {
  // Reset throttle to force cleanup
  lastCleanupTime[logPrefix] = 0;
  await cleanupStaleLogs(logPrefix);
}
