/**
 * Plugin version utilities
 *
 * Reads version from plugin.json manifest and checks for updates
 */

import { readFileSync } from "node:fs";
import { join } from "node:path";
import {
  CLAUDE_INSTALL_DIR,
  MARKETPLACE_PLUGIN_JSON_URL,
  VERSION_CHECK_TIMEOUT_MS,
} from "../config/constants.js";
import { logger } from "./logger.js";

/**
 * Structure of plugin.json from marketplace repository
 */
export interface MarketplacePluginJson {
  version: string;
  name?: string;
  description?: string;
  [key: string]: unknown; // Allow additional fields
}

/**
 * Result of version comparison
 */
export type VersionComparisonResult = "newer" | "same" | "older";

/**
 * Result of update check operation
 */
export interface UpdateCheckResult {
  updateAvailable: boolean;
  currentVersion: string;
  latestVersion: string;
  error?: string;
}

/**
 * Get the current plugin version from the marketplace plugin.json
 * @returns Plugin version string (e.g., "0.0.7") or "unknown" if unable to read
 */
export function getPluginVersion(): string {
  try {
    // Claude reads the version from the marketplace plugin.json
    const marketplacePluginPath = join(
      CLAUDE_INSTALL_DIR,
      "plugins",
      "marketplaces",
      "zest-marketplace",
      "zest",
      ".claude-plugin",
      "plugin.json",
    );

    const pluginJson = JSON.parse(readFileSync(marketplacePluginPath, "utf-8"));

    if (pluginJson.version && typeof pluginJson.version === "string") {
      logger.debug("Read plugin version from marketplace plugin.json", {
        version: pluginJson.version,
      });
      return pluginJson.version;
    }

    logger.warn("Version field not found in marketplace plugin.json");
    return "unknown";
  } catch (error) {
    logger.warn("Failed to read plugin version from marketplace plugin.json", error);
    return "unknown";
  }
}

/**
 * Fetch the latest plugin version from the marketplace repository
 * @returns Promise that resolves to version string (e.g., "0.0.41")
 * @throws Error if network request fails, times out, or response is malformed
 */
export async function fetchMarketplaceVersion(): Promise<string> {
  logger.info("Fetching latest plugin version from marketplace", {
    url: MARKETPLACE_PLUGIN_JSON_URL,
  });

  // Create AbortController for timeout
  const controller = new AbortController();
  const timeoutId = setTimeout(() => {
    controller.abort();
    logger.warn("Marketplace version fetch timed out", {
      timeout_ms: VERSION_CHECK_TIMEOUT_MS,
    });
  }, VERSION_CHECK_TIMEOUT_MS);

  try {
    // Fetch plugin.json from marketplace
    const response = await fetch(MARKETPLACE_PLUGIN_JSON_URL, {
      signal: controller.signal,
      headers: {
        Accept: "application/json",
        "User-Agent": "zest-claude-plugin",
      },
    });

    // Check HTTP status
    if (!response.ok) {
      const error = new Error(
        `Marketplace request failed: HTTP ${response.status} ${response.statusText}`,
      );
      logger.error("Failed to fetch marketplace version - HTTP error", {
        status: response.status,
        statusText: response.statusText,
        url: MARKETPLACE_PLUGIN_JSON_URL,
      });
      throw error;
    }

    // Parse JSON response
    const data = (await response.json()) as MarketplacePluginJson;

    // Validate version field exists
    if (!data.version || typeof data.version !== "string") {
      const error = new Error("Marketplace plugin.json missing or invalid version field");
      logger.error("Invalid marketplace plugin.json structure", {
        hasVersion: !!data.version,
        versionType: typeof data.version,
      });
      throw error;
    }

    logger.info("Successfully fetched marketplace version", {
      version: data.version,
    });

    return data.version;
  } catch (error) {
    // Handle specific error types
    if (error instanceof Error) {
      if (error.name === "AbortError") {
        throw new Error(`Marketplace version check timed out after ${VERSION_CHECK_TIMEOUT_MS}ms`);
      }
      // Re-throw other errors with context
      throw error;
    }
    // Handle non-Error exceptions
    throw new Error(`Unexpected error fetching marketplace version: ${error}`);
  } finally {
    // Clean up timeout
    clearTimeout(timeoutId);
  }
}

/**
 * Parse a semantic version string into components
 * @param version - Version string (e.g., "0.0.8" or "1.2.3-beta")
 * @returns Object with major, minor, patch numbers, or null if malformed
 */
function parseVersion(version: string): {
  major: number;
  minor: number;
  patch: number;
} | null {
  // Remove leading 'v' if present
  const cleanVersion = version.startsWith("v") ? version.slice(1) : version;

  // Extract base version (before any pre-release tag like "-beta")
  const baseVersion = cleanVersion.split("-")[0];

  // Split into parts and parse as integers
  const parts = baseVersion.split(".");

  if (parts.length < 1 || parts.length > 3) {
    return null;
  }

  const major = Number.parseInt(parts[0], 10);
  const minor = parts.length >= 2 ? Number.parseInt(parts[1], 10) : 0;
  const patch = parts.length >= 3 ? Number.parseInt(parts[2], 10) : 0;

  // Check if any part is NaN
  if (Number.isNaN(major) || Number.isNaN(minor) || Number.isNaN(patch)) {
    return null;
  }

  // Validate ranges - reject negative numbers
  if (major < 0 || minor < 0 || patch < 0) {
    return null;
  }

  // Validate ranges - reject unreasonably large version numbers (likely malformed)
  if (major > 9999 || minor > 9999 || patch > 9999) {
    return null;
  }

  return { major, minor, patch };
}

/**
 * Compare two semantic version strings
 * @param currentVersion - Current installed version (e.g., "0.0.1")
 * @param latestVersion - Latest available version (e.g., "0.0.8")
 * @returns "newer" if latest > current, "same" if equal, "older" if latest < current
 *
 * @example
 * compareVersions("0.0.1", "0.0.8") // returns "newer"
 * compareVersions("0.0.8", "0.0.8") // returns "same"
 * compareVersions("0.0.10", "0.0.8") // returns "older"
 * compareVersions("0.1.0", "0.0.9") // returns "newer"
 */
export function compareVersions(
  currentVersion: string,
  latestVersion: string,
): VersionComparisonResult {
  logger.debug("Comparing versions", {
    current: currentVersion,
    latest: latestVersion,
  });

  // Parse both versions
  const current = parseVersion(currentVersion);
  const latest = parseVersion(latestVersion);

  // Handle malformed versions
  if (!current || !latest) {
    logger.warn("Unable to compare versions - malformed version string", {
      current: currentVersion,
      latest: latestVersion,
      currentParsed: current,
      latestParsed: latest,
    });
    // If we can't parse, treat as "same" to avoid false notifications
    return "same";
  }

  // Compare major version
  if (latest.major > current.major) {
    return "newer";
  }
  if (latest.major < current.major) {
    return "older";
  }

  // Major versions equal, compare minor version
  if (latest.minor > current.minor) {
    return "newer";
  }
  if (latest.minor < current.minor) {
    return "older";
  }

  // Major and minor equal, compare patch version
  if (latest.patch > current.patch) {
    return "newer";
  }
  if (latest.patch < current.patch) {
    return "older";
  }

  // All components equal
  return "same";
}

/**
 * Check if a plugin update is available
 *
 * Orchestrates the full version check workflow:
 * 1. Gets current installed version from local plugin.json
 * 2. Fetches latest version from marketplace repository
 * 3. Compares versions to determine if update is available
 *
 * @returns Promise that resolves to UpdateCheckResult with status and version info
 *
 * @example
 * const result = await checkForUpdates();
 * if (result.updateAvailable) {
 *   console.log(`Update available: ${result.currentVersion} â†’ ${result.latestVersion}`);
 * }
 */
export async function checkForUpdates(): Promise<UpdateCheckResult> {
  logger.info("Starting plugin update check");

  try {
    // Step 1: Get current installed version
    const currentVersion = getPluginVersion();

    // Handle case where we can't read local version
    if (currentVersion === "unknown") {
      const error = "Unable to determine current plugin version";
      logger.warn(error);
      return {
        updateAvailable: false,
        currentVersion: "unknown",
        latestVersion: "unknown",
        error,
      };
    }

    logger.info("Current plugin version", { version: currentVersion });

    // Step 2: Fetch latest version from marketplace
    let latestVersion: string;
    try {
      latestVersion = await fetchMarketplaceVersion();
    } catch (error) {
      // Network or parsing error - return gracefully
      const errorMessage = error instanceof Error ? error.message : String(error);
      logger.warn("Failed to fetch marketplace version", { error: errorMessage });
      return {
        updateAvailable: false,
        currentVersion,
        latestVersion: "unknown",
        error: errorMessage,
      };
    }

    // Step 3: Compare versions
    const comparison = compareVersions(currentVersion, latestVersion);

    // Determine if update is available
    const updateAvailable = comparison === "newer";

    logger.info("Version check complete", {
      currentVersion,
      latestVersion,
      comparison,
      updateAvailable,
    });

    return {
      updateAvailable,
      currentVersion,
      latestVersion,
    };
  } catch (error) {
    // Catch any unexpected errors in the orchestration
    const errorMessage = error instanceof Error ? error.message : String(error);
    logger.error("Unexpected error during version check", { error: errorMessage });

    return {
      updateAvailable: false,
      currentVersion: "unknown",
      latestVersion: "unknown",
      error: `Version check failed: ${errorMessage}`,
    };
  }
}
