/**
 * Application constants and configuration values
 */

import { homedir } from "node:os";
import { join } from "node:path";

// Claude Code directories
// Respects CLAUDE_INSTALL_PATH environment variable for non-standard installations
export const CLAUDE_INSTALL_DIR = process.env.CLAUDE_INSTALL_PATH || join(homedir(), ".claude");
export const CLAUDE_PROJECTS_DIR = join(CLAUDE_INSTALL_DIR, "projects");
export const CLAUDE_SETTINGS_FILE = join(CLAUDE_INSTALL_DIR, "settings.json");

// Base directories
export const CLAUDE_ZEST_DIR = join(CLAUDE_INSTALL_DIR, "..", ".claude-zest");
export const QUEUE_DIR = join(CLAUDE_ZEST_DIR, "queue");
export const LOGS_DIR = join(CLAUDE_ZEST_DIR, "logs");
export const STATE_DIR = join(CLAUDE_ZEST_DIR, "state");
export const DELETION_CACHE_DIR = join(CLAUDE_ZEST_DIR, "cache", "deletions");

// File paths
export const SESSION_FILE = join(CLAUDE_ZEST_DIR, "session.json"); // Auth session + workspace info
export const SETTINGS_FILE = join(CLAUDE_ZEST_DIR, "settings.json"); // User preferences
export const DAEMON_PID_FILE = join(CLAUDE_ZEST_DIR, "daemon.pid");
export const STATUSLINE_SCRIPT_PATH = join(CLAUDE_ZEST_DIR, "statusline.mjs"); // Status line script (.mjs for ESM support)
export const STATUS_CACHE_FILE = join(CLAUDE_ZEST_DIR, "status-cache.json"); // Status cache for version checks and sync errors

// Queue files
export const EVENTS_QUEUE_FILE = join(QUEUE_DIR, "events.jsonl");
export const SESSIONS_QUEUE_FILE = join(QUEUE_DIR, "chat-sessions.jsonl");
export const MESSAGES_QUEUE_FILE = join(QUEUE_DIR, "chat-messages.jsonl");

// Platform and source identifiers
export const PLATFORM = "terminal";
export const SOURCE = "claude-code";
export const CLIENT_ID = "claude-cli";

// Sync configuration
export const SYNC_INTERVAL_MS = 60000; // 60 seconds
export const MAX_RETRY_ATTEMPTS = 3;
export const RETRY_BACKOFF_MS = 5000;

// File locking configuration
export const LOCK_RETRY_MS = 50; // Retry interval when lock is held by another process
export const LOCK_MAX_RETRIES = 300; // Max retries (10 seconds total)

// Debounce configuration (prevents duplicate hook executions)
export const DEBOUNCE_DIR = join(CLAUDE_ZEST_DIR, "debounce");
export const DEBOUNCE_WINDOW_MS = 500; // First-wins debounce window
export const DEBOUNCE_TRAILING_MS = 300; // Trailing debounce: wait after last hook before processing

// Delayed extraction configuration (for PostToolUse trailing debounce)
export const DELAYED_EXTRACTION_INITIAL_DELAY_MS = 500; // Initial wait before checking if hooks settled
export const DELAYED_EXTRACTION_MAX_WAIT_MS = 10000; // Maximum total wait time
export const DELAYED_EXTRACTION_CHECK_INTERVAL_MS = 300; // How often to check if hooks settled

// Cache configuration
export const DELETION_CACHE_TTL_MS = 5 * 60 * 1000; // 5 minutes

// Log rotation configuration
export const LOG_RETENTION_DAYS = 7; // Delete log files older than 7 days

// Authentication configuration
export const PROACTIVE_REFRESH_THRESHOLD_MS = 5 * 60 * 1000; // Proactively refresh JWT/access tokens 5 minutes before expiration

// Content size limits
export const MAX_DIFF_SIZE_BYTES = 10 * 1024 * 1024; // 10MB
export const MAX_CONTENT_PREVIEW_LENGTH = 1000; // characters
export const MAX_SESSION_TITLE_LENGTH = 100; // characters
export const MIN_SESSION_TITLE_LENGTH = 3; // characters

// Session quality filters
export const MIN_MESSAGES_PER_SESSION = 3; // Minimum messages required to upload a session
export const STALE_SESSION_AGE_MS = 7 * 24 * 60 * 60 * 1000; // 7 days in milliseconds

// API endpoints (configured from .env at build time)
// Values are baked into the bundle during build
export const WEB_APP_URL = process.env.ZEST_WEB_APP_URL || "http://localhost:3000";
export const SUPABASE_URL = process.env.ZEST_SUPABASE_URL || "";
export const SUPABASE_ANON_KEY = process.env.ZEST_SUPABASE_ANON_KEY || "";

// Filter patterns for commands/messages to exclude from tracking
export const EXCLUDED_COMMAND_PATTERNS = [
  // Claude Code built-in commands
  /^\/(add-dir|agents|bashes|bug|clear|compact|config|context|cost|doctor|exit|export|help|hooks|ide|init|install-github-app|login|logout|mcp|memory|model|output-style|permissions|plugin|pr-comments|privacy-settings|release-notes|resume|review|rewind|sandbox|security-review|stats|status|statusline|terminal-setup|todos|usage|vim)\b/i,

  // Zest plugin commands (like /zest:status, /zest:login, etc.)
  /^\/zest[^:\s]*:/i,

  // Messages containing Zest command tags (e.g., <command-name>/zest:status</command-name>)
  /<command-name>\/zest[^<]*<\/command-name>/i,

  // Messages containing Zest CLI script paths (e.g., "node .../dist/commands/sync-cli.js")
  /node\s+.*\/dist\/commands\/.*-cli\.js/i,
];

export const ZEST_SESSION_NAMESPACE = "1b671a64-40d5-491e-99b0-da01ff1f3341";

// Version check configuration
// If ZEST_MARKETPLACE_PLUGIN_JSON_URL is not set, version checking will be disabled
export const MARKETPLACE_PLUGIN_JSON_URL = process.env.ZEST_MARKETPLACE_PLUGIN_JSON_URL || "";
export const VERSION_CHECK_TIMEOUT_MS = 5000; // 5 seconds
export const UPDATE_CHECK_CACHE_TTL_MS = 60 * 60 * 1000; // 1 hour - how long to cache update check results

// Daemon management configuration
export const DAEMON_FRESH_PID_THRESHOLD_MS = 2000; // 2 seconds - how recently PID file must be modified to skip restart
